import {Logger} from '../src/Logger'

let instance = null;
let connection = null;

export class WebSocketClient {
    constructor() {
        Logger.Trace("WebSocketClient constructor");
        if (!instance) {
            this.WSParameter = {
                serverIP : "",
                port : "",
                keepAliveTimeResponse: 0,
                keepAliveTimeRequest: 30000,
                keepAliveMessage : "ping",
                onError : function (error){},
                onDisconnected: function (){},
                onConnected: function (){},
                onMessage: function (message) {},
                onOpen: function(){},
                onClose: function(){}
            };
            this.tm;
        }
        return instance;
    }

    manageKeepAlive(){
        if(this.WSParameter.keepAliveTimeResponse>0 && connection.readyState === 1 ){
        Logger.Debug("Keep Alive configured");
        Logger.Debug("Sending every  ["+ this.WSParameter.keepAliveTimeRequest +"] milisenconds");
        Logger.Debug("Response expected within  ["+ this.WSParameter.keepAliveTimeResponse +"] milisenconds");
           this.ping();
        }
    }

    ping(){
        var _this = this;
        this.send(this.WSParameter.keepAliveMessage);
        this.tm = setTimeout(function () {
            _this.executeOnClose();
            _this.close();
         }, this.WSParameter.keepAliveTimeResponse);
    }
    pong(){
        Logger.Trace("pong");
        var _this = this;
        clearTimeout(this.tm);
        setTimeout(function () {
            _this.ping();
         }, this.WSParameter.keepAliveTimeRequest);
    }
    connect(secureConnection = false){
        // if user is running mozilla then use it's built-in WebSocket
            window.WebSocket = window.WebSocket || window.MozWebSocket;

            // if browser doesn't support WebSocket, just show some notification and exit
            if (!window.WebSocket) {
                if(_this.WSParameter.onError){
                    this.WSParameter.onError("Sorry, but your browser doesn\'t support WebSockets.");
                }
                return;
            }
            // open connection
            let connectionType = secureConnection ? "wss" : "ws";
            connection = new WebSocket(connectionType+'://'+this.WSParameter.serverIP+':'+this.WSParameter.port);
            var _this = this;


            connection.onopen = function () {
                if(_this.WSParameter.onOpen){
                    Logger.Trace("onOpen");
                    _this.manageKeepAlive();
                    _this.WSParameter.onOpen();
                }
            };

            connection.onerror = function (error) {
                if(_this.WSParameter.onError){
                    _this.WSParameter.onError(error);
                }
            };
            
            connection.onmessage = function (message) {
                if(message.data==="pong"){
                    _this.pong();
                    return;
                }
                if(_this.WSParameter.onMessage){
                    _this.WSParameter.onMessage(message);
                }
            };
            connection.onclose = function () {            
                _this.executeOnClose();
            };
        }

        executeOnClose(){
            if(this.WSParameter.onClose){ 
                this.WSParameter.onClose();
            }
        }

        send(msg){
            if(connection&&msg){
                Logger.Trace("Sending: ",msg);
                connection.send(msg);
            }
        }

        close(){
            if(connection){
                connection.close();
                Logger.Debug("Closing connection");
            }
        }
}


