import {LoggerInstance} from '../src/lib/bower/global-logger-services-js-bundle/global-logger-services-js-bundle';

export class Logger {
	
	static Trace(...msg) {
		LoggerInstance.Trace('[Web-Socket-Client] ', ...msg);
	}

	static Debug(...msg) {
		LoggerInstance.Debug('[Web-Socket-Client] ', ...msg);
	}
	
	static Warning(...msg) {
		LoggerInstance.Warning('[Web-Socket-Client] ', ...msg);
	}

	static Info(...msg) {
		LoggerInstance.Info('[Common-Utilities] ', ...msg);
	}

	static Error(...msg){
		LoggerInstance.Error('[Common-Utilities] ', ...msg);
	}
}


